body {
    align-items: center;
    justify-content: center;
}

.coin-app {
    display: flex;
    justify-content: center;
}

.coin-row {
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 900px;
}

.coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 200px;
}

.coin h1 {
    font-size: 16px;
    width: 150px;
}

.coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
}

.coin-symbol {
    text-transform: uppercase;
}

.coin-data {
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
}

.coin-price {
    width: 110px;
}

.coin-volume {
    width: 200px;
}

.coin-marketcap {
    width: 240px;
}

.coin-percent {
    width: 80%;
}

.red {
    color: red;
}

.green {
    color: green;
}

@media (max-width: 500px) {
    .coin-marketcap, .coin-volume{
        display: none;
    }

    .coin-row {
        width: 350px;
    }

    .coin, .coin-data {
        padding-right: 6px;
        width:100px;
    }

    .price-change {
        margin: 0px 0px 0px 10px;
    }
}